@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif !important;
}

/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Playfair Display', serif; */

/* //navbar css */
.navbar {
  padding: 5px;
  padding-right: 10px;
  font-family: "Poppins", sans-serif;
  background-color: #2f2f2f !important;
  border-bottom: 0.5px solid gray;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
}

.logo {
  height: 40px;
  margin: 3px;
}

.btn {
  background-color: #0059b3 !important;
  border: none;
}

.btn:hover {
  background-color: #2e96ff !important;
}
.btn,
.Searchbar {
  border-radius: 5px !important;
}
.nav-search {
  margin-top: 6px !important;
}
.nav-link {
  font-weight: 300 !important;
  color: white !important;
}

.nav-link:hover {
  color: #2e96ff !important;
}
.ant-badge {
  color: white !important;
}
.ant-badge:hover {
  color: #007fff !important;
}

.dropdown-item {
  color: #2f2f2f !important;
}
.active {
  border-bottom: 2px solid white;
}

.navbar-brand {
  font-weight: 700;
  font-family: "roboto", sans-serif;
  letter-spacing: 3px;
}
/* ------------------------------------------------------------- */

.search-form {
  margin-right: 2em;
  margin-top: 3em;
}
.search-form input {
  border: none;
  border-radius: 0;
}
.search-form button {
  background-color: #000000;
  border-radius: 0;
  color: white;
}

.search-item {
  margin-top: 2em;
  border: 2px solid black;
  border: none;
  border-radius: 10px !important;
  background-color: #2f2f2f;
  padding: 1em;
}

.ant-radio-wrapper span {
  color: white !important;
  font-size: large !important;
}

/* ================================ */
/* country */
.country {
  gap: 1em;
  margin-left: 5px;
  display: flex;
  justify-content: space-between;
  margin-top: auto;
  margin-left: 1em;
}

.Country-Card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  opacity: 40%;
  z-index: -1;
}

.Country-div {
  text-align: center;
  color: #2f2f2f !important;
  font-size: 2em;
  font-family: Arial, Helvetica, sans-serif !important;
}
@media (max-width: 620px) {
  .country {
    display: flex;
    flex-direction: column;
  }
}
.card-containt {
  position: relative;
  color: white !important;
  overflow: hidden;
  height: 20em;
  border: 1px solid black;
  border-radius: 10px;
  border: none;
  flex: 1;
  cursor: pointer;
}

.featuredImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.featuredImg:hover {
  scale: 110%;
  transition-duration: 1000ms;
}

.featuredTitles {
  position: absolute;
  bottom: 20px;
  left: 10px;
}
.featuredTitles h1 {
  font-size: 1.4em !important;
  font-weight: bold;
  color: white !important;
}
.featuredTitles h2 {
  font-size: 1em !important;
  color: white !important;
}
/* =============================
==========footer============= */

.footer {
  color: white;
  padding: 2em;
  height: 15%;
  background: #2f2f2f; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #434343,
    #000000
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #434343, #000000);
}

.footer h1 {
  font-size: 2em;
}
.footer a {
  text-decoration: none;
  font-size: large !important;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: #007fff;
  transition-duration: 500ms;
}
/* =================================
======== page not found css ======= */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}
/* ========================================= */
/* =========contact us ========= */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ==================== */
.product-link {
  text-decoration: none !important;
  color: black !important;
}

.cat-btn {
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
}
.cat-btn:hover {
  background-color: #434343;
  color: white;
}
/* ================
====dashboard
=================== */

.dashboard {
  margin-top: 100px !important;
}

.dashboard-menu h4 {
  background-color: #434343 !important;
  color: white;
  padding: 20px 0px;
}
/* ------------------------------------- */
/* Reg */
.form-container form {
  border-radius: 20px !important;
}

.Reg-p {
  margin-left: 4px;
  font-weight: bold;
  font-size: large;
}
/* --------------------------------------------- */
.forgetPass {
  height: 85vh;
}
/* ================================================= */
/* Services */
.services h1 {
  font-size: 2em;
  color: #2f2f2f !important;
  
}
/* --------------------------------------------------- */
.description p {
  font-size: larger;
  color: #2f2f2f !important;
}

.description h1 {
  font-size: 3em;
}

.desc-pic {
  background-color: #2f2f2f;
  margin-right: 3em;
}

.contactus {
  height: 85vh !important;
}
/* --------------------------------- */
.sliderh1 {
  width: 100%;
  padding-left: 2rem;
  text-align: left;
  z-index: 10;
  font-weight: bolder;
  text-shadow: #434343;
  text-shadow: 0.5px 0.1px;
}
.react-slideshow-container {
  object-fit: fill;
}
@media (max-width: 925px) {
  .slide-container {
    display: none;
  }
  .Country-div {
    margin-top: 2em;
  }
}
